<template>
	<div>
		<header>
			<router-link to='/fbrw' class="goback"></router-link>
			<p>发布任务</p>
		</header>
		<div class="content">
			<div class="c_m">
				<div class="c_b">
					<p class="c_ti">选择考核班级</p>
					<div class="c_b_l">
						<div v-for='(c, i) in c_list' :key='i' :class='c.id | dSye(cd_list)'>
							<input :value="c.id" type="checkbox" name="" :id='"bj"+i' v-model='cd_list'>
							<label :for='"bj"+i'><p>{{c.name}}</p></label>
						</div>
					</div>
				</div>
				<div class="c_k">
					<p class="c_ti">选择考核方式</p>
					<div class="a_mth">
						<div v-for='(a, i) in a_list' :key='a.name'>
							<input :id='"a"+i' class="a_c" type="checkbox" name="a_mth" :value='a.val' v-model='khfs' :style='a.val | inSye(khfs)'>
							<label :for='"a"+i'>
								<p class="a_name" :style='a.val | pSye(khfs)'>{{a.name}}</p>
							</label>
						</div>
					</div>
				</div>
				<div class="c_py">
					<p class="c_ti">阅读要求(选填)</p>
					<textarea placeholder="您的推荐能帮助学生更好地理解本次任务..." v-model='ydyq'></textarea>
					<button></button>
				</div>
				<router-link to='/fbrw/ts' class='c_nt'>下一步</router-link>
				<p class="c_t">共3步</p>
			</div>
		</div>
		<tips :msg='message' v-show='t_flag'></tips>
	</div>
</template>

<script>
	var timer=null;
	import tips from './msgtips.vue'
	export default {
		data(){
			return {
				// 班级列表
				c_list: [],
				// 选中班级
				cd_list: [],
				// 班级
				cl_flag: -1,
				// 考核方式
				khfs: [],
				a_list: [
					{val: 2, name: '写读后感'},
					{val: 1, name: '评测'}
				],
				// 阅读要求
				ydyq: '',
				// 提示框
				message: '',
				t_flag: false
			}
		},
		created: function(){
			var self=this;
			// --------------请求班级列表begin--------------
			// 先判断全局变量中是否有数据，若无则向后台请求数据
			if(!this.util.getData('clist')){
				$.ajax({
					url: self.globalurl.host+self.globalurl.url.bjlb,
					data: {"loginAccount": self.tId, "token": 124567898},
					dataType: 'json',
					type: 'POST',
					success: function(data){
						if(data.statusCode==200&&data.classes.length!=0){
							// for(let i=0;i<data.classes.length;i++){
							// 	self.c_list.push(data.classes[i]);
							// }
							self.c_list=data.classes;
							// alert(self.c_list[0].name);
							self.util.setData('cid', self.c_list[0].id);
							self.util.setData('clist', self.c_list);
						}
					}
				})
			}else{
				this.c_list=this.util.getData('clist');
			}
			// --------------请求班级列表end--------------
		},
		// 导航加载钩子
		beforeRouteEnter(to, from, next){
			if(from.path=='/fbrw/ts'||from.path=='/fbrw'){
				// 当从下一步返回到该页面时，恢复原有信息
				next(function(vm){
					var rwInfo=vm.util.getSessionData("rwinfo");
					if(rwInfo.bj&&rwInfo.khfs){
						vm.cd_list=rwInfo.bj.split(',');
						vm.khfs=rwInfo.khfs.split(',');
						vm.ydyq=rwInfo.ydyq;
					}
				})
			}else{
				next();
			}
		},
		// 导航离开钩子
		beforeRouteLeave(to, from, next){
			if(to.path=='/fbrw/ts'){
				if(this.cd_list.length==0||this.khfs.length==0){
					var self=this;
					clearTimeout(timer);
					timer=null;
					this.message='请填写重要信息';
					this.t_flag=true;
					timer=setTimeout(function(){
						self.t_flag=false;
					}, 2000);
					next(false);
				}else{
					var rwInfo=this.util.getSessionData("rwinfo");
					rwInfo.bj=this.cd_list.join();
					rwInfo.khfs=this.khfs.join();
					rwInfo.ydyq=this.ydyq;
					this.util.setSessionData("rwinfo", rwInfo);
					next();
				}
			}else{
				next();
			}
		},
		components: {
			"tips": tips
		},
		directives: {
			binit: {
				inserted: function(el){
					var calendar = new lCalendar();
					calendar.init({
					    'trigger': '#t_begin',
					    'type': 'datetime'
					});
				}
			},
			einit: {
				inserted: function(el){
					var calendar = new lCalendar();
					calendar.init({
					    'trigger': '#t_end',
					    'type': 'datetime'
					});
				}
			}
		},
		filters: {
			inSye: function(val){
				var flag=false;
				for(let i=0;i<arguments[1].length;i++){
					if(arguments[1][i]==val){
						flag=true;
						break;
					}
				}
				if(flag){
					return {backgroundImage: "url('../../static/images/radio_0ff.png')"}
				}else{
					return {backgroundImage: "url('../../static/images/Radio_no.png')"}
				}
			},
			pSye: function(val){
				var flag=false;
				for(let i=0;i<arguments[1].length;i++){
					if(arguments[1][i]==val){
						flag=true;
						break;
					}
				}
				if(flag){
					return {color: "#777777"}
				}else{
					return {color: "#b7b7b7"}
				}
			},
			dSye: function(val){
				var flag=false;
				for(let i=0;i<arguments[1].length;i++){
					if(arguments[1][i]==val){
						flag=true;
						break;
					}
				}
				if(flag){
					return "bg";
				}else{
					return "";
				}
			}
		}
	}
</script>

<style scoped>
	.content{
		position: absolute;
		top: 0.88rem;
		bottom: 0px;
		width: 100%;
		background-color: #fff;
		padding: 0.3rem;
	}
	.c_m{
		width: 100%;
		height: 100%;
		overflow: auto;
	}
	.c_b,.c_k{
		width: 100%;
		margin-bottom: 0.8rem;
	}
	.c_k{
		margin-top: 0.3rem;
	}
	.c_b_l{
		width: 100%;
		display: flex;
		flex-wrap: wrap;
	}
	.c_b_l div{
		margin-top: 0.2rem;
		margin-left: 0.25rem;
		border: 0px;
		background-color: #fff;
		border: 0.01rem solid #777777;
		border-radius: 0.1rem;
	}
	.c_b_l div.bg{
		background-color: #ddfdeb;
		background-image: url('../../static/images/xg.png');
		background-size: 0.35rem 0.35rem;
		background-repeat: no-repeat;
		background-position: right top;
	}
	.c_b_l div p{
		padding: 0.2rem 0.3rem;
		text-align: center;
		font-size: 0.35rem;
	}
	.c_b_l div input{
		display: none;
	}
	.c_py{
		position: relative;
		margin-top: 0.8rem;
		width: 100%;
	}
	.c_ti{
		border-left: 0.1rem solid #40cc8b;
		font-size: 0.35rem;
		padding-left: 0.1rem;
	}
	.c_py textarea{
		margin-top: 0.3rem;
		width: 100%;
		resize: none;
		border-radius: 0.1rem;
		height: 3rem;
		border: 0.01rem solid #e7e7e7;
		padding: 0.2rem;
		font-size: 0.33rem;
	}
	.c_py button{
		border: 0px;
		background-color: rgba(0,0,0,0);
		position: absolute;
		bottom: 0.2rem;
		right: 0.15rem;
		width: 0.45rem;
		height: 0.65rem;
		background-image: url('../../static/images/ico_02.png');
		background-size: 100% 100%;
	}
	.c_nt{
		display: block;
		width: 3rem;
		height: 1rem;
		background-color: #40cc8b;
		color: #fff;
		font-size: 0.35rem;
		text-align: center;
		line-height: 1rem;
		border-radius: 3rem;
		margin: 0 auto;
		margin-top: 0.3rem;
	}
	.c_t{
		margin-top: 0.2rem;
		text-align: center;
		font-size: 0.3rem;
		color: #777777;
	}
	.a_mth{
		overflow: auto;
		overflow-y: hidden;
		display: box;
		display: -webkit-box;
		width: 100%;
		height: 0.54rem;
		margin-top: 0.33rem;
	}
	.a_c{
		-webkit-appearance: none;
		background-image: url('../../static/images/Radio_no.png');
		background-size: 100% 100%;
		width: 0.54rem;
		height: 0.54rem;
		border: 0px;
	}
	.a_c:checked{
		background-color: #fff;
	}
	.a_name{
		display: inline-block;
		height: 0.54rem;
		line-height: 0.54rem;
		font-size: 0.42rem !important;
		vertical-align: top;
		color: #b7b7b7;
		margin-right: 0.8rem;
	}
	.a_add,.a_n_list{
		position: relative;
		width: 100%;
	}
	.a_add input{
		width: 100%;
		height: 0.9rem;
		border: 0px;
		border-bottom: 0.01rem solid #e7e7e7;
		padding-left: 0.2rem;
	}
	.a_add a{
		display: block;
		width: 0.5rem;
		height: 0.5rem;
		border: 0px;
		background-color: #fff;
		background-image: url('../../static/images/ico_24.png');
		background-size: 100% 100%;
		position: absolute;
		top: 50%;
		right: 0.2rem;
		margin-top: -0.25rem;
	}
	.a_checed{
		color: #111111;
	}
</style>


